<template>
  <!-- @mouseleave="dataLazyLoad" -->
  <!-- @scroll="handleScroll" -->
  <div class="watch" ref="srcollView" @scroll="handleScroll($event)">
    <div class="nav" :class="{isHide:isHide}">
      <van-nav-bar :title="this.title" left-arrow @click-left="onClickLeft" fixed />
    </div>
    <div class="container" v-for="(imgItem,imgIndex) in imagesData" :key="imgIndex">
      <img v-lazy="imgItem" />
    </div>

    <!-- 章节面板 -->
    <div class="select-item">
      <van-action-sheet v-model="show" title="章节">
        <div class="content">
          <div
            class="list-item"
            v-for="(epItem,epIndex) in cartoonItemData"
            :key="epIndex"
            :name="epItem.id"
            @click="goLookCartoon(epItem.id,epItem.title)"
          >
            {{epItem.short_title}}
            <div class="icon-box">
              <van-icon color="#f9a018" :name="epItem.is_locked === true ? 'lock' : '' " />
            </div>
          </div>
        </div>
      </van-action-sheet>
    </div>
    <!-- 显示章节按钮 -->
    <div class="ShowBtn">
      <van-button plain type="primary" @click="isShow">
        <van-icon name="apps-o" />
      </van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Watch",
  data() {
    return {
      // 漫画path接口
      pathData: [],
      // 漫画图片
      imagesData: [],
      // 章节名
      title: "",

      oldScrollTop: 0,
      isHide: false,

      loading: false,
      finished: false,

      // 当前漫画id
      nowIndetailId: "",
      // 当前漫画章节id
      ThisId: "",
      // 所有章节id
      ListIdData: [],
      // 当前章节下标
      index: "",
      // 章节面板显示
      show: false,
      // 章节数据
      cartoonItemData: []
    };
  },
  created() {
    // 当前漫画id
    this.nowIndetailId = this.$route.params.nowIndetailId;
    this.ThisId = this.$route.params.id;
    this.title = this.$route.params.title;
    // 当前章节下标
    this.$root.ListIdData.forEach((item, index) => {
      if (item.id === this.ThisId) {
        this.index = index;
      }
    });
    //  
    this.GetImageIndex(this.ThisId);

    //   

    //  获取所有章节信息
    //  
    this.cartoonItemData = this.$root.epListData;
  },
  methods: {
    // 页面滚动事件
    handleScroll(e) {
      const { scrollTop, clientHeight, scrollHeight } = e.target;
      if (scrollTop + clientHeight == scrollHeight) {
        

        //   this.imagesData = [];
        this.index = Number(this.index) + 1;

        if (Number(this.index) + 1 > this.$root.ListIdData.length) {
          return;
        }
        
        this.ThisId = this.$root.ListIdData[this.index].id;
        this.title = this.$root.ListIdData[this.index].title;

        this.GetImageIndex(this.ThisId);
      }
    },
    isShow() {
      this.show = true;
    },

    // 返回漫画详情
    onClickLeft() {
      this.$router.push({
        name: "Indetail",
        params: {
          id: this.nowIndetailId
        }
      });
    },
    //   获取漫画图片
    GetImageIndex(id) {
      this.axios({
        method: "get",
        url: "/GetImageIndex",
        params: {
          epId: id
        }
      })
        .then(res => {
          //   
          let imagesIndex = res.data.data.images;
          let host = res.data.data.host;
          this.pathData = [];
          imagesIndex.forEach(v => {
            let path = host + v.path + "@660w.jpg";
            this.pathData.push(path);
          });
          this.axios({
            method: "get",
            url: "/ImageToken",
            params: {
              urls: JSON.stringify(this.pathData)
            }
          })
            .then(res => {
              this.imagesData = [];
              let ImgPath = res.data.data;
              for (let i in ImgPath) {
                let token = ImgPath[i].token;
                let url = ImgPath[i].url;
                let img = url + "?token=" + token;
                this.imagesData.push(img);
              }
            })
            .catch(err => {
              
            });
        })
        .catch(err => {
          
        });
    },
    // 点击查看章节内容
    goLookCartoon(id, title) {
      this.title = title;
      this.ThisId = id;
      this.GetImageIndex(this.ThisId);
      this.$refs.srcollView.scrollTop = 0;
    }
  }
};
</script>

<style lang="scss" scoped>
.watch {
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  overflow-y: scroll;
  .ShowBtn {
    position: fixed;
    bottom: 20px;
    left: 20px;
  }
}
.isHide {
  transform: translateY(0px);
}
.container {
  img {
    display: block;
    width: 100%;
  }
}
.select-item {
  .content {
    display: flex;
    flex-wrap: wrap;
    .list-item {
      width: calc(25% - 7px);
      text-align: center;
      box-sizing: border-box;
      padding: 10px;
      font-size: 15px;
      border-radius: 0.25rem;
      border: 1px solid #ccc;
      margin-right: 9px;
      margin-bottom: 5px;
      position: relative;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      cursor: pointer;
      &:nth-child(4n) {
        margin-right: 0;
      }
      .icon-box {
        width: 10%;
        height: 10%;
        position: absolute;
        top: -5%;
        right: 10%;
      }
    }
  }
}
.van-button--plain {
  background-color: rgba(30, 30, 30, 0.5);
  width: 15px;
  height: 28px;
}
.van-button--plain.van-button--primary {
  color: skyblue;
}
.van-button--primary {
  border: 0.02rem solid skyblue;
}
::v-deep .van-nav-bar {
  background-color: rgba($color: #000000, $alpha: 0.8);
  color: white;
}
::v-deep .van-nav-bar__title {
  color: white;
}
::v-deep .van-nav-bar .van-icon {
  color: white;
}
</style>